@import url("https://fonts.googleapis.com/css?family=Muli&display=swap");
@import "./mixins.scss";
@import "./colors.scss";
@import "./fonts.scss";

body {
  width: 100vw;
  height: 100vh;
  font-family: "nimbus-sans", sans-serif;
  color: $gray;
  font-size: 0.65rem;
  overflow-x: hidden;
  background: $off-white;
  @include for-tablet-portrait-up {
    font-size: 1rem;
  }
}

#container {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;

  @include for-tablet-landscape-up {
    display: unset;
  }

  canvas {
    width: 100%;
    height: 100%;
  }
}

.background {
  z-index: -1;
}

.hide-image {
  @include for-tablet-landscape-up {
    opacity: 0;
  }
}

.layout {
  max-width: 1200px;
  margin: auto;
}

.page-content {
  margin: 0 1rem;
}

[data-scroll-section] {
  margin-bottom: 5rem;

  @include for-tablet-landscape-up {
    margin-bottom: 10rem;
  }
}

.overlay {
  top: 0;
  right: 0;
  z-index: 50;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: $white;
}

.navbar {
  width: 100%;

  @include for-tablet-landscape-up {
    display: unset;
  }

  &__list {
    display: flex;
    border-bottom: solid 1px $gray;
    align-items: center;
    padding: 1.5rem .5rem;
    @include for-tablet-landscape-up {
      padding: 2rem 0;
    }
  }
  &__item {
    font-size: $font-normal;
    font-weight: 300;

    &:not(:last-child) {
      margin-right: 1rem;
    }

    @include for-tablet-landscape-up {
      margin-right: 2rem !important;
    }

    &--name {
      margin-right: auto;
      @include for-tablet-portrait-up {
        opacity: 0;
      }
    }
  }

  &__link {
    background: none;
    border: none;
    cursor: pointer;
    font-weight: 400;
    position: relative;
    color: $gray;
    text-decoration: none;
    display: block;
    padding: 0 0.25rem 0.25rem;

    &:after {
      bottom: 0;
      right: 0;
      left: 0;
      content: "";
      position: absolute;
      background: $red;
      width: 0;
      height: 3px;
      transition: width 0.25s ease-in-out;
    }
    &:hover {
      &:after {
        width: 100%;
      }
    }
  }
}

.landing {
  position: relative;
  margin-top: 2em;
  width: auto;
  height: 50vw;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  grid-template-rows: 1fr 2fr;
  overflow: visible;

  @include for-tablet-landscape-up {
    height: 50%;
    padding: 0;
    grid-template-columns: 4fr 2fr 2fr;
    grid-template-rows: 1fr 3fr;
  }

  &__image {
    object-fit: cover;
    grid-row: 1 / 3;
    grid-column: 1 / 3;
    height: 100%;
    width: 100%;
    position: relative;
  }

  &__header {
    color: $black;
    font-size: $font-xl;
  }

  &__info {
    margin-bottom: 0.5em;
  }

  &__text {
    z-index: 1;
    grid-column: 2 / 4;
    grid-row: 2 / 4;
    background: $white;
    padding: 2em;
    margin-bottom: auto;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  }

  &::after {
    z-index: -1;
    content: "";
    position: absolute;
    right: 100%;
    top: 0;
    bottom: 50%;
    width: 10px;
    background: $red;
  }
}

.watermark {
  z-index: -1;
  display: none;
  position: fixed;
  bottom: 10%;
  right: -1.5rem;

  @include for-tablet-portrait-up {
    display: unset;
  }

  &__text {
    background: $white;
    text-transform: uppercase;
    position: relative;
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=1);

    &::before {
      position: absolute;
      bottom: 3rem;
      content: "";
      background: $red;
      height: 3rem;
      width: 100%;
    }
  }
}

.project-list {
  max-width: 30rem;
  margin: auto;
  @include for-tablet-landscape-up {
    max-width: 100%;
  }
}

.project-card {
  position: relative;
  display: flex;
  flex-direction: column;

  @include for-tablet-landscape-up {
    max-width: 50%;
    flex-direction: row;
  }

  &--right {
    .project-card__content {
      right: 75%;
    }
    @include for-tablet-landscape-up {
      flex-direction: row-reverse;
    }
  }
  &--left {
    .project-card__content {
      left: 75%;
    }
  }
  &:not(:last-child) {
    margin-bottom: 15%;
  }
  &:nth-child(2n) {
    @include for-tablet-landscape-up {
      margin-left: auto;
    }
  }
  &__header {
  }

  &__content {
    @include for-tablet-landscape-up {
      width: 100%;
      padding: 0 1rem 1rem;
      position: absolute;
      background: $white;
      box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
      bottom: 0;
    }
  }

  h3 {
    position: relative;
    padding-top: 1rem;
    font-size: $font-large;
    color: $black;
    margin-bottom: 0.5rem;

    @include for-tablet-landscape-up {
      display: inline-block;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 10px;
      width: 100%;
      background: $red;
    }
  }

  &__wrapper {
    width: 100%;
  }

  img {
    width: 100%;
    object-fit: contain;
  }

  &__svg {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
  }
}

.about {
  padding-bottom: 30%;
  @include for-tablet-landscape-up {
    display: flex;
    width: 50rem;
    margin: auto;
  }

  &__left {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    margin: 0 auto;
    
    @include for-tablet-portrait-up {
      width: 50%;
    }
  }
  &__right {
    flex: 1;
    padding: 1rem;
  }
  &__email {
    font-size: $font-large;
    margin-bottom: 1rem;
  }
  &__img {
    width: 50%;
    margin: 0 auto;
    @include for-tablet-landscape-up {
      width: 100%;
    }
  }
  &__cta {
    text-align: center;
    font-size: $font-xl;
    color: $black;
    margin-bottom: 1em;
    @include for-tablet-landscape-up {
      text-align: left;
    }
  }
}

.social-media {
  &__link {
    position: relative;
    overflow: hidden;
    a {
      display: flex;
      align-items: center;
      text-decoration: none;
      padding: 0.5rem 0;
      color: $gray;
      transition: all 0.25s ease-in-out;
      svg {
        stroke: $gray;
        transition: all 0.25s ease-in-out;
      }
      
      *:first-child {
        margin-right: 1rem;
      }
    }
    
    &:after {
      content: "";
      z-index: -1;
      position: absolute;
      margin-top: auto;
      bottom: 0;
      top: 0;
      right: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background: $gray;
      transition: all .25s ease-in-out;
    }
    &:hover {
      a {
        color: $white;
        padding: 0.5rem .5rem;
      }
      svg {
        stroke: $white
      }

      &:after {
        background: $red;
        height: 100%;
      }
    }
  }
}
